import { Buffer } from "buffer";
import { keccak256 } from "ethereumjs-util";
import { OrderedMap } from "immutable";
import MerkleTree from "merkle-tree-solidity";
import { useCallback, useEffect } from "react";
import AbiCoder, { AbiCoder as AbiCoderType } from "web3-eth-abi";

import { useEthers } from "@usedapp/core";

import { ClaimABI, getContract, useClaimContract } from "./useContract";
import { useContractFunction } from "./useContractFunction";
import { UseContractState } from "./useContractState";

export const useClaim = () => {
  const { account, chainId, library } = useEthers();
  const { epochDetails } = UseContractState.useContainer();

  const claimContract = useClaimContract(chainId)!;

  const { send, state } = useContractFunction(claimContract, "claim");

  const claim = useCallback(
    async (epochId: number, epochSeriesIndex: number) => {
      const epoch = epochDetails.get(epochId);
      if (!account || !epoch) {
        return;
      }

      const operators = OrderedMap(epoch.operators);
      const abiCoder = AbiCoder as unknown as AbiCoderType;

      const elements = operators.map((count, address) => {
        const concatenated = Buffer.from(
          abiCoder
            .encodeParameters(
              ["address", "uint256"],
              [address.toLowerCase(), count]
            )
            .slice(2),
          "hex"
        );
        return keccak256(concatenated);
      });

      const merkleTree = new MerkleTree(elements.valueSeq().toArray());

      const merkleRoot = merkleTree.getRoot();

      const share = operators.get(account.toLowerCase());

      const hash = keccak256(
        Buffer.from(
          abiCoder
            .encodeParameters(
              ["address", "uint256"],
              [account.toLowerCase(), share]
            )
            .slice(2),
          "hex"
        )
      );

      const proof = merkleTree.getProof(hash);

      return await send(
        `Claim Epoch ${epochId}-${epochSeriesIndex + 1}`,
        epochId,
        proof,
        share,
        [epochSeriesIndex]
      );
    },
    [account, epochDetails, send]
  );

  return {
    claim,
    state,
  };
};
