import { useEffect, useState } from "react";
import { isMobile } from "react-device-detect";

import { useEthers } from "@usedapp/core";
import { InjectedConnector } from "@web3-react/injected-connector";

import { PolygonNetworks } from "./types";

export const injected = new InjectedConnector({
  supportedChainIds: [PolygonNetworks.Mainnet, PolygonNetworks.Mumbai],
});

/**
 * Automatically connect the injected web3 provider if it has already been
 * authorized.
 */
export function useConnectWeb3OnLoad() {
  const { activateBrowserWallet, account } = useEthers();
  const [tried, setTried] = useState(false);

  useEffect(() => {
    injected.isAuthorized().then((isAuthorized) => {
      if (!account && isAuthorized) {
        activateBrowserWallet();
      } else {
        if (isMobile && (window as any).ethereum) {
          activateBrowserWallet();
        } else {
          setTried(true);
        }
      }
    });
  }, [activateBrowserWallet]); // intentionally only running on mount (make sure it's only mounted once :))

  // if the connection worked, wait until we get confirmation of that to flip the flag
  useEffect(() => {
    if (account) {
      setTried(true);
    }
  }, [account]);

  return tried;
}
