import React, { useCallback, useMemo } from "react";
import styled from "styled-components";

import { UseContractState } from "../hooks/useContractState";
import PreviewContainer from "../hooks/usePreviewContainer";
import { TransferModal } from "../modals/TransferModal";
import Card, { RenderArtwork } from "../stateless/Card";
import { Epoch, MediaType, NFT } from "../types/NFT";
import { UseModal } from "./ModalView";

const CollectionDiv = styled.div`
  display: flex;
  flex-flow: column;
  background: ${(props) => props.theme.colors.bg};
  color: ${(props) => props.theme.colors.fg};
  min-height: 100vh;

  padding-left: 180px;
  padding-right: 180px;

  @media (max-width: ${(props) => props.theme.grid.maxXl}) {
    padding-left: 100px;
    padding-right: 100px;
  }

  @media (max-width: ${(props) => props.theme.grid.maxLg}) {
    padding-left: 80px;
    padding-right: 80px;
  }

  @media (max-width: ${(props) => props.theme.grid.maxMd}) {
    padding-left: 30px;
    padding-right: 30px;
  }

  @media (max-width: ${(props) => props.theme.grid.maxSm}) {
    padding-left: 20px;
    padding-right: 20px;
  }

  padding-top: 100px;

  @media (max-width: ${(props) => props.theme.grid.maxSm}) {
    padding-top: 40px;
  }
`;
const Cards = styled.div`
  display: flex;
  flex-wrap: wrap;
`;

const RenderFullPageInner = ({
  next,
  previous,
}: {
  next: (() => void) | undefined;
  previous: (() => void) | undefined;
}) => {
  const image = (image: string): NFT => ({
    name: "",
    url: image,
    mediaType: MediaType.Image,
    count: 0,
  });

  const { setImage } = PreviewContainer.useContainer();

  const images = [
    [
      image("/images/16/1/media.jpg"),
      image("/images/16/1/media-2.jpg"),
      image("/images/16/1/media-3.jpg"),
    ],

    [
      image("/images/16/2/media.jpg"),
      image("/images/16/2/media-2.jpg"),
      image("/images/16/2/media-3.jpg"),
      image("/images/16/2/media-4.jpg"),
    ],

    [
      image("/images/16/3/media.jpg"),
      image("/images/16/3/media-2.jpg"),
      image("/images/16/3/media-3.jpg"),
    ],
  ];

  const epochCards = images.map((edition, i) => {
    const editionCards = edition.map((nft) => {
      const onClick = (event: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
        event.preventDefault();
        event.stopPropagation();
        // setShowAsModal((current) => !current)
        setImage(<RenderArtwork nft={nft} preview={true} />);
      };
      return <Card onClick={onClick} nft={nft} />;
    });

    return (
      <div>
        <h4 style={{ marginBottom: 10, marginTop: 20 }}>#{i + 1}</h4>
        <div
          style={{
            width: "100%",
            display: "flex",
            flexWrap: "wrap",
            justifyContent: "flex-start",
            alignItems: "center",
          }}
        >
          {editionCards}
        </div>
      </div>
    );
  });

  return (
    <CollectionDiv>
      <Cards>
        <div
          style={{
            width: "100%",
            marginBottom: "50px",
          }}
        >
          <h2 style={{ marginBottom: 40 }}>Epoch 16</h2>
          {epochCards}
        </div>
      </Cards>
    </CollectionDiv>
  );
};

export const Full16 = () => {
  return <RenderFullPageInner next={undefined} previous={undefined} />;
};
